.header {
  background: #121212;
}
.header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
}
.header h1 {
  position: relative;
  font-size: 0px;
  line-height: 60px;
}

.header h1 img {
  height: 40px;
}
.header nav > ul {
  display: flex;
}
.header form {
  position: relative;
  display: flex;
  width: 55%;
  align-items: center;
  margin-left: 70px;
}
.header input {
  height: 30px;
  width: 100%;
  padding-left: 10px;
  border-radius: 3px;
  border: none;
}
.header button {
  position: absolute;
  right: 5px;
  bottom: 0px;
  font-size: 20px;
  background: none;
  border: none;
  cursor: pointer;
}
.header .pro {
  margin-left: 10px;
  margin-right: auto;
}
.header .pro a {
  padding: 10px 15px;
}
.header .pro a:hover {
  background: #333;
  border-radius: 5px;
}
.header .pro img {
  height: 18px;
}
.header .right {
  gap: 10px;
}
.header .right a {
  padding: 5px 10px;
  border-radius: 5px;
}
.header .right a:hover {
  background: #333;
}
.header .menuBtn i {
  font-size: 20px;
  vertical-align: -40%;
  margin-left: 10px;
}

.mainVisual .index {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  padding: 15px 0 5px 0;
  line-height: 20px;
}
.mainVisual .index .left {
  font-size: 15px;
  color: var(--gb);
}
.mainVisual .index .left span {
  margin: 0 5px;
  font-size: 13px;
  vertical-align: 2%;
}
.mainVisual .index .left i {
  font-size: 15px;
}
.mainVisual .index .right ul {
  display: flex;
  gap: 20px;
}
.mainVisual .index .right i {
  font-size: 19px;
  vertical-align: -15%;
}
.mainVisual .index .right li {
  position: relative;
}
.mainVisual .index .right li::after {
  content: "";
  position: absolute;
  right: -10px;
  top: 50%;
  width: 1px;
  transform: translateY(-50%);
  height: 100%;
  background: #444;
}
.mainVisual .index .right li:last-of-type::after {
  display: none;
}
.mainVisual .index .right li a {
  padding: 5px 10px;
  border-radius: 5px;
}
.mainVisual .index .right li a:hover {
  background: #333;
}
.mainVisual .content .info {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
}
.mainVisual .content .box {
  display: flex;
  align-items: center;
}
.mainVisual .content .info .right {
  white-space: nowrap;
}
.mainVisual .content .info i {
  font-size: 20px;
}
.mainVisual .content .info i.star {
  margin-right: 5px;
  color: var(--yc);
}
.mainVisual .content .info i.tDown {
  margin-right: 5px;
  color: #fb3c3c;
}
.mainVisual .content .info span i {
  font-size: 13px;
}
.mainVisual .popularity span {
  margin-left: 5px;
  font-size: 13px;
  color: var(--gb);
}
.mainVisual .content .left h2 {
  font-size: 36px;
  text-indent: -2px;
  margin-bottom: 10px;
}
.mainVisual .content .left p {
  font-size: 13px;
  font-weight: 300;
  color: #ddd;
}
.mainVisual .content ul {
  display: flex;
  font-size: 13px;
  font-weight: 300;
  color: #ddd;
  gap: 17px;
}
.mainVisual .content .left li {
  position: relative;
}
.mainVisual .content .left li ~ li > span::before {
  content: "";
  position: absolute;
  top: 55%;
  left: -9px;
  height: 2px;
  width: 2px;
  transform: translateY(-50%);
  border-radius: 50%;
  background: #ddd;
}
.mainVisual .content .left p span {
  position: relative;
  margin-right: 15px;
}
.mainVisual .content .left p span ~ span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -9px;
  height: 2px;
  width: 2px;
  transform: translateY(-50%);
  border-radius: 50%;
  background: #ddd;
}
.mainVisual .content .right {
  display: flex;
  gap: 30px;
  align-items: flex-end;
}
.mainVisual .content .right .box {
  display: flex;
}
.mainVisual .content .right > div {
  flex: 1;
}
.mainVisual .visual {
  display: flex;
  gap: 10px;
  padding-bottom: 20px;
}
.mainVisual .visual img:hover {
  filter: brightness(0.9);
}
.mainVisual .visual .left figure {
  height: 500px;
  width: 320px;
  border-radius: 5px;
}
.mainVisual .visual .left figure img {
  height: 100%;
  border-radius: 5px;
}
.mainVisual .visual .center {
  position: relative;
}
.mainVisual .visual .center figure {
  background: url(../public/img/mainVisual/m02.jpg) no-repeat center center/cover;
  height: 500px;
  width: 870px;
  border-radius: 5px;
}
.mainVisual .visual .center figure:hover {
  filter: brightness(0.9);
}
.mainVisual .visual .inner {
  position: absolute;
  bottom: 0;
  display: flex;
  margin: 20px;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: 300;
}
.mainVisual .visual .inner div {
  border: 2px solid rgb(200, 200, 200);
  border-radius: 50%;
}
.mainVisual .visual .inner:hover div {
  border: 2px solid #f5c518;
}
.mainVisual .visual .inner:hover i {
  color: var(--yc);
}
.mainVisual .visual .inner i {
  font-size: 50px;
  transform: translateX(3px);
}
.mainVisual .visual .inner span {
  transform: translateX(3px);
  font-size: 15px;
  font-weight: 300;
  letter-spacing: 1px;
}
.mainVisual .des {
  display: flex;
}
.mainVisual .des .left {
  flex: 5;
}
.mainVisual .des .right {
  flex: 2;
}
.mainVisual .des .genre {
  padding-bottom: 30px;
}
.mainVisual .des .genre ul {
  display: flex;
  gap: 10px;
}
.mainVisual .des .genre a {
  padding: 5px 10px 7px 10px;
  font-weight: 300;
  border: 1px solid #ddd;
  border-radius: 20px;
}
.mainVisual .des .genre a:hover {
  background: #444;
}
.mainVisual .des .storyline {
  width: 890px;
  font-weight: 300;
  line-height: 1.35;
  padding-bottom: 30px;
}
.mainVisual .des .storyline strong {
  font-size: 21px;
  font-weight: 400;
  margin-bottom: 5px;
  padding-bottom: 0;
  color: #fff;
}
.mainVisual .des .storyline p {
  font-size: 13px;
  font-weight: 300;
  padding-top: 0;
}
.mainVisual .des .produce {
  display: flex;
  gap: 30px;
  padding-right: 60px;
  padding-bottom: 30px;
  font-weight: 300;
}
.mainVisual .des .produce figure {
  border-radius: 5px;
}
.mainVisual .des .produce > div {
  position: relative;
}
.mainVisual .des .creator {
  flex: 1;
}
.mainVisual .des .stars {
  flex: 3;
}
.mainVisual .des .info {
  flex: 1;
}
.mainVisual .des .produce > div ~ div::before {
  content: "";
  position: absolute;
  left: -15px;
  height: 100%;
  width: 1px;
  background: #333;
}
.mainVisual .des > div strong {
  display: block;
  font-size: 15px;
  margin-bottom: 10px;
  font-weight: 300;
  color: #fff;
}
.mainVisual .des figure {
  margin-bottom: 2px;
}
.mainVisual .des .creator figure {
  background: url(../public/img/mainVisual/Vince-Gilligan-Sony.jpg) no-repeat center center/cover;
  height: 100px;
  width: 150px;
}
.mainVisual .des .produce .info img {
  height: 30px;
}
.mainVisual .des .stars .box {
  display: flex;
  gap: 10px;
}
.mainVisual .des a {
  color: rgb(238, 238, 238);
  font-size: 13px;
}
.mainVisual .des .creator a:hover,
.mainVisual .des .stars a:hover {
  text-decoration: underline;
}
.mainVisual .des .star figure {
  height: 100px;
  width: 150px;
  margin-bottom: 5px;
}
.mainVisual .des .star01 figure {
  background: url(../public/img/mainVisual/bryan02.jpg) no-repeat center center/cover;
}
.mainVisual .des .star02 figure {
  background: url(../public/img/mainVisual/aaron.jpg) no-repeat center center/cover;
}
.mainVisual .des .star03 figure {
  background: url(../public/img/mainVisual/anna02.jpg) no-repeat center center/cover;
}
.mainVisual .des .info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  line-height: 1.35;
}
.mainVisual .des .info p {
  position: relative;
  font-weight: 100;
  padding-left: 10px;
}
.mainVisual .des .info p::before {
  content: "";
  bottom: 5px;
  left: 0;
  position: absolute;
  display: inline-block;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: #ddd;
}
.mainVisual .des .info a:first-of-type {
  margin-bottom: 10px;
}
.mainVisual .des .info a:last-of-type {
  display: inline-block;
  margin-top: 10px;
  margin-right: auto;
  padding: 3px 20px 5px 20px;
  font-size: 13px;
  border: 1px solid #ddd;
  color: #ddd;
  border-radius: 5px;
}
.mainVisual .des .info a:last-of-type:hover {
  background: #444;
}
.mainVisual .topRate {
  height: 60px;
  padding: 20px;
  text-align: center;
  border: 1px solid #333;
  overflow: hidden;
  transition: 0.5s;
}
.mainVisual .topRate.on {
  height: 200px;
}
.mainVisual .topRate strong {
  display: block;
  margin-bottom: 5px;
  padding-left: 15px;
  font-size: 17px;
  cursor: pointer;
}
.mainVisual .topRate > p {
  opacity: 0;
  transition: 0.2s;
}
.mainVisual .topRate.on > p {
  opacity: 1;
}
.mainVisual .topRate.on i {
  transform: rotate(180deg);
}
.mainVisual .topRate > p {
  display: inline-block;
  margin-bottom: 20px;
  font-size: 13px;
  font-weight: 300;
  color: #ddd;
}
.mainVisual .topRate .awards p {
  font-size: 13px;
  text-transform: uppercase;
}
.mainVisual .topRate .awards ul {
  display: flex;
  justify-content: center;
  gap: 50px;
}
.mainVisual .topRate .awards figure {
  background: url(../public/img/mainVisual/pen_america.png) no-repeat center center/cover;
  height: 80px;
  width: 80px;
  margin: 0 auto;
  margin-bottom: 5px;
}
.mainVisual .awards a {
  font-size: 13px;
  font-weight: 300;
}
.mainVisual .awards a:hover {
  text-decoration: underline;
}

.episodes .content {
  margin-bottom: 40px;
}
.episodes .content > ul {
  display: flex;
  gap: 30px;
  margin-bottom: 10px;
}
.episodes .content > ul .box {
  display: flex;
  margin-bottom: 10px;
}
.episodes .content > ul > li {
  padding: 20px;
  background: #121212e3;
  border-radius: 10px;
  gap: 30px;
}
.episodes .content .box figure {
  height: 150px;
  width: 130px;
  border-radius: 10px;
}
.episodes .content .box01 figure {
  background: url(../public/img/episodes/e01.jpg) no-repeat center center/cover;
}
.episodes .content .box02 figure {
  background: url(../public/img/episodes/e02.jpg) no-repeat center center/cover;
}
.episodes .content .box03 figure {
  background: url(../public/img/episodes/e03.jpg) no-repeat center center/cover;
}
.episodes .right {
  margin-left: 15px;
}
.episodes .right > p {
  display: block;
  padding-bottom: 5px;
}
.episodes .right span {
  display: block;
  margin-bottom: 30px;
  font-size: 13px;
  font-weight: 300;
  color: var(--gb);
}
.episodes .right a:hover {
  text-decoration: underline;
}
.episodes .right a h3 {
  font-size: 13px;
  font-weight: 300;
  margin-bottom: 3px;
}
.episodes .right a p {
  font-size: 21px;
