}
.episodes .txt p {
  margin-bottom: 10px;
}
.episodes .txt i {
  margin-right: 3px;
  color: var(--yc);
  vertical-align: -10%;
}
.episodes .filter {
  font-size: 13px;
  font-weight: 300;
  color: #ddd;
}
.episodes .filter a {
  margin-left: 10px;
  padding: 3px 15px;
  color: rgb(95, 175, 255);
