}
.episodes .moments {
  display: flex;
  gap: 30px;
  line-height: 1.4;
}
.episodes .moments .left {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.episodes .moments h3 {
  position: relative;
  display: inline-block;
  font-size: 15px;
  margin-top: 10px;
}
.episodes .moments h3 span {
  font-size: 25px;
}
.episodes .moments h3::before {
  content: "";
  position: absolute;
  top: -10px;
  height: 2px;
  width: 30px;
  background: #f5c518;
  border-radius: 4px;
}
.episodes .moments p {
  font-size: 14px;
  font-weight: 300;
  color: #ddd;
  line-height: 1.5;
  margin-bottom: 30px;
}

.episodes .moments a span {
  font-size: 13px;
  font-weight: 100;
  color: #ddd;
  vertical-align: -5%;
}
.episodes .moments a span:hover {
  text-decoration: underline;
}
.episodes .moments img {
  margin-right: 3px;
  font-size: 0;
  height: 28px;
}
.episodes .moments i {
  margin-left: 3px;
  font-size: 13px;
}

.gallery ul {
  display: flex;
  gap: 30px;
}
.gallery ul li {
  position: relative;
}
.gallery .photo {
  border-radius: 5px;
}
.gallery figure {
  height: 400px;
  width: calc(1110px / 4);
}
.gallery .photo01 {
  background: url(../public/img/gallery/g04.jpg) no-repeat center center/cover;
}
.gallery .photo02 {
  background: url(../public/img/gallery/g01.jpg) no-repeat center center/cover;
}
.gallery .photo03 {
  background: url(../public/img/gallery/g02.jpg) no-repeat center center/cover;
}
.gallery .photo04 {
  background: url(../public/img/gallery/g03.jpg) no-repeat center center/cover;
}
.gallery figure {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.gallery figure:hover::after {
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.gallery .inner {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 15px;
  background: rgba(0, 0, 0, 0.8);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.gallery .inner > a {
  display: block;
  font-weight: 300;
  margin-bottom: 8px;
}
.gallery .inner p {
  font-size: 13px;
  font-weight: 300;
  color: rgb(221, 221, 221);
}
.gallery .inner p > a {
  color: #5fafff;
}
.gallery .inner a:hover {
  text-decoration: underline;
}

.cast ul {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.cast li {
  display: flex;
  padding: 5px;
  background: #141414;
  border-radius: 10px;
  width: calc(1200px / 2 - 15px);
}
.cast li figure {
  height: 110px;
  width: 80px;
  border-radius: 10px;
}
.cast li:nth-child(5n + 1) figure {
  background: url(../public/img/cast/bryan.jpg) no-repeat center center/cover;
}
.cast li:nth-child(5n + 2) figure {
  background: url(../public/img/cast/aaron.jpg) no-repeat center center/cover;
}
.cast li:nth-child(5n + 3) figure {
  background: url(../public/img/cast/anna.jpg) no-repeat center center/cover;
}
.cast li:nth-child(5n + 4) figure {
  background: url(../public/img/cast/betsy.jpg) no-repeat center center/cover;
}
.cast li:nth-child(5n + 5) figure {
  background: url(../public/img/cast/dean.jpg) no-repeat center center/cover;
}
.cast li .right {
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: 1.618;
}
.cast li strong {
  font-size: 19px;
}
.cast li span {
  font-size: 15px;
  font-weight: 100;
  color: var(--gb);
}
.cast li p {
  display: inline-block;
  font-size: 13px;
  font-weight: 100;
  color: var(--gb);
}
.cast li i {
  margin-left: 3px;
  font-size: 13px;
  color: var(--gb);
}
.cast li a:hover {
  text-decoration: underline;
}

.reviews .content ul {
  display: flex;
  gap: 30px;
}
.reviews .content ul > li {
  padding: 20px;
  border: 1px solid #333;
  border-radius: 10px;
  line-height: 1.35;
  flex: 1;
}
.reviews .content .top {
  display: flex;
  justify-content: space-between;
}
.reviews .content .top span {
  display: inline-block;
  margin-bottom: 5px;
  padding: 3px 15px 3px 15px;
  font-size: 11px;
  font-weight: 500;
  color: #000;
  background: #f5c518;
  border-radius: 5px;
}
.reviews .content .top i {
  font-size: 20px;
  color: rgb(247, 247, 247);
}
.reviews .content h3 {
  font-size: 19px;
  margin-bottom: 10px;
}
.reviews .content h3:hover {
  text-decoration: underline;
}
.reviews .content p {
  font-size: 15px;
  font-weight: 300;
  color: var(--gb);
  letter-spacing: -0.025;
}
.reviews .content .txt {
  margin-bottom: 20px;
}
.reviews .content .evaluate {
  display: flex;
  vertical-align: middle;
}
.reviews .content .evaluate a:first-of-type {
  margin-right: 20px;
}
.reviews .content .evaluate a:last-of-type i {
  color: var(--yc);
}
.reviews .info {
  display: flex;
  justify-content: space-between;
}
.reviews .content .evaluate i {
  font-size: 20px;
  margin-right: 3px;
