}

.reviews .content .evaluate span {
  font-size: 13px;
  font-weight: 300;
  vertical-align: 18%;
}
.reviews .content .userName {
  font-size: 12px;
  font-weight: 300;
  color: var(--gb);
}
.reviews .content .userName a {
  color: #5fafff;
}
.reviews .content .userName a:hover {
  text-decoration: underline;
}
.reviews .content .userName a::after {
  content: "";
  display: inline-block;
  margin: 0 5px 0 5px;
  width: 2px;
  height: 2px;
  background: #ddd;
  border-radius: 50%;
  vertical-align: 22%;
  cursor: default;
}

.similar .content figure {
  position: relative;
}
.similar ul {
  display: flex;
  gap: 30px;
