.similar .stars {
  font-size: 15px;
  font-weight: 300;
  line-height: 1.35;
  margin-bottom: 10px;
}
.similar .stars span {
  margin-right: 10px;
  white-space: nowrap;
}
.similar .stars a {
  margin-right: 10px;
