}
.similar .btn a {
  flex: 1;
  font-size: 13px;
  font-weight: 300;
  padding: 5px 10px 6px 15px;
  background: #313131;
  border-radius: 5px;
}
.similar .btn a:hover {
  background: #444;
}
.similar .btn i {
  margin-left: 2px;
  vertical-align: 0;
  transform: translateX(-1px);
}

footer {
  background: #121212;
  padding: 40px 0 90px 0;
}
.footer .container {
  line-height: 1.618;
  color: rgb(221, 221, 221);
}
.footer .container .content {
  display: flex;
  margin-bottom: 30px;
}
.footer .left,
.footer .right {
  flex: 1;
}
.footer .left > div,
.footer .right {
  transition: 0.3s;
}
.footer .left > div:hover h3,
.footer .right:hover h3 {
  color: var(--yc);
}
.footer h3 {
  position: relative;
  display: inline-block;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 10px;
}
.footer .left address {
  font-size: 12px;
  font-weight: 300;
}
.footer .top {
  margin-bottom: 60px;
}
.footer .sns {
  display: flex;
  font-size: 23px;
  gap: 10px;
}
.footer .right li {
  font-size: 13px;
  font-weight: 300;
}
.footer .services {
  display: flex;
  flex-wrap: wrap;
  line-height: 2;
}
.footer .services a:hover {
  text-decoration: underline;
}
.footer .services li {
  width: 300px;
}
.footer .copyright {
  font-size: 12px;
  color: #999;
}
